.greeting-form {
  padding: 30px;
  margin: 20px auto;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 90%;
  max-width: 600px; /* Limit the width on larger screens */
}

.greeting-form h2 {
  font-size: 2rem;
  color: #8b0000;
  text-align: center;
  margin-bottom: 20px;
}

.greeting-form form {
  display: flex;
  flex-direction: column;
  gap: 20px; /* Space between form elements */
}

.greeting-form input,
.greeting-form textarea {
  width: 100%; /* Ensure the inputs take full width of the form */
  padding: 12px; /* Increase padding for better touch area */
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-sizing: border-box; /* Include padding and border in the element’s width */
}

.greeting-form textarea {
  height: 150px; /* Set height for the textarea */
  resize: vertical; /* Allow resizing of the textarea only vertically */
}

.greeting-form button {
  padding: 12px;
  font-size: 1.2rem;
  color: white;
  background-color: #8b0000;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.greeting-form button:hover {
  background-color: #b22222; /* Darker red on hover */
}

/* Responsive Design for Mobile */
@media (max-width: 768px) {
  .greeting-form {
    padding: 20px;
    max-width: 100%; /* Ensure the form takes full width on small screens */
  }

  .greeting-form h2 {
    font-size: 1.5rem; /* Adjust font size for mobile */
  }

  .greeting-form input,
  .greeting-form textarea {
    font-size: 0.9rem;
    padding: 10px;
  }

  .greeting-form button {
    font-size: 1rem;
    padding: 10px;
  }
}
