/* Roka Teaser Container */
.roka-teaser-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  max-width: 800px;
  margin: 0 auto;
}

/* Teaser Header */
.teaser-header h3 {
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold;
  color: #D35400;
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

/* Video Container */
.video-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

/* Iframe Styling */
iframe {
  border-radius: 8px;
  width: 100%; /* Make iframe responsive to the container width */
  height: 400px; /* Default height */
  max-width: 100%; /* Prevent iframe from exceeding the container width */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  aspect-ratio: 16 / 9; /* Keep aspect ratio consistent */
}

/* Mobile Specific Styling */
@media (max-width: 768px) {
  /* Adjust text size and spacing for smaller screens */
  .teaser-header h3 {
    font-size: 1.2rem;
    padding: 8px;
    margin-bottom: 10px;
  }

  /* Adjust iframe height for better mobile view */
  iframe {
    height: 250px; /* Adjust height for smaller screens */
    width: 100%; /* Ensure it takes full width */
  }
}
