.about-couple-card {
  display: flex;
  gap: 20px;
}

/* Ensure there's enough space between sections */
.about-section {
  display: flex;
  flex: 1;
  align-items: center;
  padding: 0 20px;
  justify-content: space-between; /* Space between text and image */
  margin-bottom: 30px; /* Added bottom margin to create space between groom and bride sections */
}

.about-text {
  flex: 1;
  margin-right: 20px; /* Right margin for text section */
  text-align: left; /* Ensure left alignment */
}

.about-text h2 {
  font-family: 'Playfair Display', serif;
  font-size: 2.5em; /* Slightly larger heading */
  color: #D35400;
}

.about-text p {
  font-size: 1.2em;
  color: #555;
  line-height: 1.8;
  text-align: left; /* Ensure left alignment */
}

.about-image {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.about-image img {
  width: 300px; /* Larger size for desktop */
  height: auto; /* Maintain aspect ratio */
  max-width: 100%; /* Ensure the image doesn't overflow */
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .about-couple-card {
    flex-direction: column; /* Stack sections vertically */
    padding: 10px; /* Reduce padding for mobile */
  }

  .about-section {
    flex-direction: column; /* Stack text and image vertically */
    padding: 10px 0;
    text-align: left; /* Keep text left-aligned */
    margin-bottom: 20px; /* Added margin for mobile to create space */
  }

  .about-text {
    margin-right: 0; /* No margin for mobile */
    margin-bottom: 20px; /* Add space between text and image */
    text-align: left; /* Keep text left-aligned */
  }

  .about-text h2 {
    font-size: 2em; /* Slightly smaller heading for mobile */
  }

  .about-text p {
    font-size: 1.1em; /* Reduce paragraph size slightly for mobile */
  }

  .about-image img {
    width: 175px; /* Smaller size for mobile */
    height: auto; /* Maintain aspect ratio */
    max-width: 100%; /* Ensure the image doesn't overflow */
  }
}
