/* Container for confetti */
.floating-confetti {
    position: fixed; /* Use fixed positioning to cover the entire viewport */
    top: 0;
    left: 0;
    width: 100vw; /* Use viewport width */
    height: 100vh; /* Use viewport height */
    pointer-events: none; /* Prevent interaction with confetti */
    overflow: hidden;
  }
  
  /* Confetti piece styles */
  .confetti-piece {
    position: absolute;
    width: 10px; /* Default width of each confetti piece */
    height: 10px; /* Default height of each confetti piece */
    background-color: #ffcc00; /* Bright gold color for festive feel */
    opacity: 0.9;
    animation: fall 5s infinite ease-in-out;
    border-radius: 50%; /* Rounded edges for the confetti pieces */
  }
  
  /* Additional colors for diversity */
  .confetti-piece:nth-child(odd) {
    background-color: #e63946; /* Vibrant red */
  }
  
  .confetti-piece:nth-child(even) {
    background-color: #f1faee; /* Soft white */
  }
  
  /* Confetti falling animation */
  @keyframes fall {
    0% {
      transform: translateY(-100px) rotate(0deg);
      opacity: 1;
    }
    100% {
      transform: translateY(100vh) rotate(360deg);
      opacity: 0.5;
    }
  }
  
  /* Optional: different animation timings for variety */
  .confetti-piece:nth-child(odd) {
    animation-duration: 6s; /* Different duration for odd pieces */
  }
  
  .confetti-piece:nth-child(even) {
    animation-duration: 7s; /* Different duration for even pieces */
  }
  
  /* Add shapes for a more festive look */
  .confetti-piece.star {
    background-color: transparent;
    clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
  }
  
  .confetti-piece.heart {
    background-color: #ff6b6b; /* A beautiful pink for heart-shaped confetti */
    clip-path: path("M16 4.41C16 2.79 14.67 1.5 13 1.5C11.34 1.5 10 2.79 10 4.41C10 6.19 12 8.07 16 11.78C20 8.07 22 6.19 22 4.41C22 2.79 20.67 1.5 19 1.5C17.34 1.5 16 2.79 16 4.41Z");
  }
  
  /* Add a class to make star and heart shapes */
  .floating-confetti .confetti-piece.star {
    width: 12px; /* Make stars a bit larger */
    height: 12px;
  }
  
  .floating-confetti .confetti-piece.heart {
    width: 12px; /* Make hearts larger */
    height: 12px;
  }
  
  /* Mobile Specific Styles */
  @media (max-width: 768px) {
    .confetti-piece {
      width: 8px; /* Smaller size for mobile devices */
      height: 8px;
    }
  
    .floating-confetti .confetti-piece.star,
    .floating-confetti .confetti-piece.heart {
      width: 10px; /* Make shapes smaller on mobile */
      height: 10px;
    }
  
    /* Adjust animation duration for mobile to ensure smoothness */
    .confetti-piece:nth-child(odd) {
      animation-duration: 5s; /* Faster duration for better performance */
    }
  
    .confetti-piece:nth-child(even) {
      animation-duration: 6s; /* Faster duration for better performance */
    }
  }
  