.about-family-card {
  padding: 40px 20px; /* Padding around the card */
}

.family-columns {
  display: flex; /* Use flexbox for side-by-side columns */
  justify-content: space-between; /* Space out the columns */
  gap: 20px; /* Gap between columns */
}

.about-section {
  flex: 1; /* Each section takes equal width */
  display: flex; /* Flex layout for text and image */
  align-items: center; /* Center items vertically */
  background-color: #fff; /* Background for each section */
  border-radius: 10px; /* Rounded corners for a softer look */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  padding: 20px; /* Padding within each section */
}

.about-text {
  flex: 1; /* Allow text section to take available space */
  padding: 20px; /* Padding for text */
}

.about-image {
  flex: 0 0 200px; /* Fixed width for the image section */
}

.about-image img {
  width: 100%; /* Responsive image */
  border-radius: 10px; /* Match the border radius */
}

.about-section.mehras {
  background-color: #f9f9f9; /* Different background for Mehra's family */
}

.about-section.seth {
  background-color: #f9f9f9; /* Different background for Seth's family */
}

/* Responsive styles */
@media (max-width: 768px) {
  .family-columns {
    flex-direction: column; /* Stack columns on smaller screens */
  }

  .about-section {
    margin-bottom: 20px; /* Space between stacked sections */
  }

  .about-image {
    width: 100%; /* Full width for images on small screens */
    margin-bottom: 20px; /* Space below images */
  }
}
