.contact-container {
    padding: 30px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 90%;
    max-width: 800px;
  }
  
  .contact-details {
    display: flex;
    justify-content: space-between; /* Align items parallelly */

  }
  
  .contact-card {
    text-align: center;
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 8px;
  }
  
  .contact-card h3 {
    margin-bottom: 10px;
  }
  
  .contact-card p {
    font-size: 1.1rem;
  }
  