.venue-container {
  margin: 30px 0;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 90%;
  max-width: 800px;
}

.venue-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.venue-card {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.venue-card h3 {
  font-size: 1.6rem;
  margin-bottom: 10px;
}

.venue-card p {
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.map-container {
  width: 100%;
  border-radius: 8px;
  overflow: hidden;
}

.map-container iframe {
  border: 0;
  border-radius: 8px;
}

.desc {
  font-size: 1.2em;
  color: #555;
  line-height: 1.8;
  text-align: left; /* Ensure left alignment */
  /* Justify text for a neat appearance */
}

/* Media Query for Mobile */
@media (max-width: 768px) {
  .venue-card h3 {
      font-size: 1.4rem;
  }

  .venue-card p {
      font-size: 1rem;
  }

  .map-container iframe {
      height: 150px; /* Reduce height for smaller screens */
  }
}
