/* General Styles */
body {
  font-family: 'Playfair Display', serif;
  background: linear-gradient(to bottom, #fffaf0, #fff1e6);
  margin: 0;
  padding: 0;
  color: #4a4a4a;
}

/* Typography for Titles */
h1, h2 {
  color: #b22222;
  text-align: center;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
}

h1 {
  font-size: 3rem;
  margin-bottom: 10px;
}

h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}

/* Dashboard Container */
.dashboard {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

/* Navigation Styles */
nav {
  margin: 20px 0;
  display: flex;
  justify-content: space-around;
  gap: 20px;
  flex-wrap: wrap;
}

nav a {
  display: inline-block;
  padding: 15px 25px;
  background-color: #d2691e;
  color: white;
  text-decoration: none;
  font-size: 1.3rem;
  border-radius: 8px;
  transition: all 0.3s ease;
  box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.15);
}

nav a:hover {
  background-color: #ff6347;
  transform: translateY(-5px);
}

/* Confetti Animation */
.floating-confetti {
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
}

.floating-confetti div {
  position: absolute;
  width: 15px;
  height: 15px;
  background-color: #ffcccb;
  opacity: 0.7;
  animation: fall 5s infinite ease-in-out;
}

.floating-confetti div:nth-child(1) { left: 10%; animation-delay: 0s; }
.floating-confetti div:nth-child(2) { left: 30%; animation-delay: 1s; }
.floating-confetti div:nth-child(3) { left: 50%; animation-delay: 2s; }
.floating-confetti div:nth-child(4) { left: 70%; animation-delay: 3s; }
.floating-confetti div:nth-child(5) { left: 90%; animation-delay: 4s; }

@keyframes fall {
  0% { transform: translateY(-100px) rotate(0deg); }
  100% { transform: translateY(100vh) rotate(360deg); }
}

/* Media Query for Mobile (max-width: 768px) */
@media (max-width: 768px) {
  nav {
    display: grid;
    grid-template-columns: 1fr 1fr; /* Create two equal columns */
    gap: 10px; /* Add space between the columns */
  }

  nav a {
    padding: 12px 20px;
    font-size: 1.1rem;
    text-align: center;
  }

  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.5rem;
  }
}
